import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  ColumnsProps,
  DefaultSortProps,
  enumFormatter,
  FilterType,
  getExtraData,
  OrderType,
  referenceColumn,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMLF } from '../../../../dependencies/empresaMLFDependency';
import { CLASS_NAME, ValorContabilMLF } from '../../../../models/dtos/mlf/valorContabilMLF';
import { TIPO_VALOR_CONTABIL_ENUM_VALUES } from '../../../../models/enumerated/mlf/tipoValorContabilEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'referencia',
    order: OrderType.DESC,
  },
];

const ValorContabilMLFSearchView: FC<SearchViewProps<ValorContabilMLF>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    referenceColumn({
      dataField: 'referencia',
      editable: false,
      sort: true,
      text: 'Referência',
    }),
    {
      dataField: 'tipoInt',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      formatExtraData: TIPO_VALOR_CONTABIL_ENUM_VALUES,
      formatter: enumFormatter,
      sort: false,
      text: 'Tipo',
    },
    {
      dataField: 'valor',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Valor',
    },
  ];

  return (
    <WindowComponent
      title="Manutenção de Valores Contábeis"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMLF(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'ValorContabilMLFEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default ValorContabilMLFSearchView;
